eg)}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background-color:white;box-shadow:2px 2px 50px grey;overflow:scroll;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:all 0.5s ease;opacity:0}.library h2{padding:2rem}.library__song{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;border-radius:50px;transition:background 0.5s ease;padding:1rem 2rem 1rem 2rem;cursor:pointer}.library__song img{width:30%}.library__song:hover{background-color:khaki}.song__description{padding-left:1rem;font-family:'Lato', sans-serif}.song__description h3{font-size:1rem}.song__description h4{font:0.7rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background:rgba(155,155,155,0.5);border-radius:20px;border:transparent}.selected{background-color:violet}.active-library{-webkit-transform:translateX(0%);transform:translateX(0%);opacity:1}@media screen and (max-width: 768px){.library{width:100%}}nav{min-hegiht:10vh;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}nav button{margin:2px 2px;background:transparent;border:none;cursor:pointer;border:2px solid #414141;padding:0.5rem;transition:all 0.3s ease}nav button:hover{background:#414141;color:white}@media screen and (max-width: 768px){nav button{z-index:10}}

